import { Injectable } from '@angular/core';
import { IStudentResponse, StudentsData } from '../models/studentData';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators'
import { ResponseEntity } from '../models/responseEntity';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { }

  public GetUserData(): Observable<IStudentResponse> {
    return this.http.get<IStudentResponse>('https://localhost:44306/api/student/get-all-students')
      .pipe(map(r => {
        var response = new ResponseEntity(r);
        console.log(response);
        if(response) {
          if(response.statusCode == 200) {

            var data = new StudentsData().withResponse(response.Data);
            console.log("data", data)
            return data;
          }
          else {
          
            return new ResponseEntity(r).Data;
          }
        }
        else {
          new ResponseEntity(r).Data;
        }
      }));
  }
}
