
export class StudentsData {
    public Data: Array<any> = [];


    withResponse(response: any) :StudentsData  {
        this.Data = response;
        return this;
    }
}

export interface IStudentData{
    id: number;
    name: string;
    rollNo: number;
}

export interface IStudentResponse {
    statusCode: number, 
    isError: boolean, 
    Description: string, 
    Data: IStudentData[]
}